import { backTop } from '$utils/back-top';
import { refreshScroll } from '$utils/refresh-scroll';

import {
  initFaqAccordion,
  initLoad,
  initNavControl,
  initNavigation,
  initSwiperAbout,
  initValue,
} from './animations/4humens_personal';
import { initAos } from './animations/4humens_personal';
import { initSplitHead, initSplitLines, splitLines } from './animations/4humens_personal';
import { initLenis } from './plugins/lenis';
import { initTrMarquee } from './plugins/scroll-marquee';

CustomEase.create('primary-ease', '0.62, 0.05, 0.01, 0.99');
CustomEase.create('primary-ease-out', '0.17,0.84,0.44,1');
// ScrollTrigger.defaults({
//   invalidateOnRefresh: true,
// });

function initApp() {
  const mm = gsap.matchMedia();

  initLenis();
  backTop();
  initTrMarquee();
  initFaqAccordion();
  initAos();
  initSwiperAbout();
  initValue();
  initLoad();
  initNavigation();
  initNavControl();
  document.fonts.ready.then(() => {
    initSplitLines();
    initSplitHead();
    splitLines();
  });
}

document.addEventListener('DOMContentLoaded', () => {
  initApp();
});
